html,
body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-size: 62.5%;
  font-weight: 500;

}


.header {
  font-size: 3rem;
  margin: 3rem auto;
  text-align: center;
}

section{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  flex-direction:column;
  margin-right:5rem;
  align-items: center;
}
.block{
  align-items: center;
}
.colorPage{
  width: 30vw;
  height: 40vh;
  border: 3px black solid;
  background-color: transparent;
  font-size: 0rem;
  text-align: center;
}
.testPage{
  width: 30vw;
  height: 40vh;
  border: 3px black solid;
  background-color: transparent;
  font-size: 0rem;
  text-align: center;
}
.rgbName{
  font-size: 2rem;
  text-align: center;
}
.enterForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50vw;
  margin: 1rem auto;
}
#enterArea {
  width: 30vw;
  height: 5vh;
  font-size: 2rem;
  margin: 3rem auto;
}
.submit-button {
  color: rgb(224, 54, 82);
  font-size: 1.5rem;
  border-radius: 3rem;
  padding:1.8%;
  margin-top: -2rem;
}

.reset{
    color: rgb(224, 54, 82);
    font-size: 1.5rem;
    border-radius: 3rem;
    padding:1.8%;
    margin-top: -2rem;
}

.buttons{
  display: flex;
  flex-direction: row;
  align-items: center;
  width:20vw;
  height:15vh;
}

.history{
  width: 50vw;
  height: 40vh;
  font-size: 2rem;
  text-align: center;
}
.historyArr{
  font-size: 2rem;
  text-align: center;
  color: transparent;
}
